import React from 'react'


export const Button = (props) => { //Button dan gelen prop lar.

    return( 
    <button {...props}>{props.text}</button>  /*...props yazarak gelen bütün propları otomatik aldık. 
    App.js de ji Button içine bir sürü html property si konulabilir. onclick, onchange, hower, vs. gibi. Bütün bunlar için tek
    tek prop almaktansa buradaki gibi spread operatörüyle almak daha mantıklı. {...props } diyerek tüm propslar eklendi  */
    )
}